import * as d3 from 'd3';
import { legendColor, legendSize } from 'd3-svg-legend';

//let data = d3.tsvParse(fs.readfileSync('data/data.tsv'),'utf-8')
const path = require('./data/data.tsv')

d3.tsv(path).then(function(data) {

  let nodes = d3.nest()
    .key((d)=>d.name)
    .entries(data)

  const width = 700,
        height = 700;

  const margin = {top: 20, right: 20, bottom: 20, left: 20},
    chartWidth = width - margin.left - margin.right,
    chartHeight = height - margin.top - margin.bottom;

  const orbitRadiusDomain = d3.extent(nodes, (d)=>d.values.length)
  const orbitRadius = d3.scaleLinear().domain(orbitRadiusDomain).range([chartWidth/2, 50]);

  const nodeRadiusDomain = d3.extent(nodes, (d)=>{
    return d3.mean(d.values,(m)=>m.relationship_value)
  })

  const nodeRadius = d3.scaleSqrt().domain(nodeRadiusDomain).range([2,5]);

  const continent = d3.map(data, (d)=>d.continent).keys().filter((d)=>d)
  let lists = d3.map(data, (d)=>d.relationship).keys()
  lists = ['All'].concat(lists)

  const colorScale = d3.scaleOrdinal(d3.schemeSet2).domain(continent).unknown('#ccc')

  const chart = d3.select('#viz').append('svg')
     .attr('width', width)
     .attr('height', height)
       .append("g")
       .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

  let mozilla = chart.append('text')
    .attr('x',chartWidth/2)
    .attr('y',chartWidth/2)
    .attr('text-anchor', 'middle')
    .text('Mozilla')

  let orbits =  chart.selectAll(".orbit")
    .data([1,2,3,4,5])
    .enter().append("circle")
    .attr('class','orbit')
    .attr('r', orbitRadius)
    .attr('cx',chartWidth/2)
    .attr('cy',chartWidth/2)
    .attr('stroke','#ddd')
    .attr('stroke-dasharray','3,3')
    .attr('fill','none')

  let node = chart
  .append("g")
  .attr("transform", "translate(" + chartWidth/2 + "," + chartWidth/2 + ")")
  .selectAll(".org")
  .data(nodes)
  .enter().append("circle")
    .attr('class','org')
    .attr("r", (d)=>{
      return nodeRadius(d3.mean(d.values,(m)=>m.relationship_value))
    })
    .attr("fill", (d)=>colorScale(d.values[0].continent))

  let simulation = d3.forceSimulation(nodes)
      .force("charge", d3.forceCollide().radius((d)=>{
        return nodeRadius(d3.mean(d.values,(m)=>m.relationship_value)) + 2
      }))
      .force("r", d3.forceRadial((d)=>orbitRadius(d.values.length)))
      .on("tick", ticked);

  const legendContinentCont = d3.select('#legend')
    .append('div')
    .append('svg')
    .append("g")
    .attr("class", "legendContinent")
    .attr("transform", "translate(20,20)");

  const legendContinent = legendColor()
    .title('Continent:')
    .scale(colorScale)


  legendContinentCont.call(legendContinent);

  const legendCircleCont = d3.select('#legend')
    .append('div')
    .append('svg')
    .append("g")
      .attr("class", "legendCircle")
      .attr("transform", "translate(20,20)");

  const legendCircle = legendSize()
    .shape('circle')
    .labelFormat(d3.format(".0f"))
    .scale(nodeRadius)
    .title('Relation strength:')

  legendCircleCont.call(legendCircle);

  d3.select('#buttons')
  .selectAll(".filter")
    .data(lists.sort())
    .enter()
    .append("div")
    .attr('class', 'filter ba dib mh1 mv2 ph3 pv2  br-pill')
    .text((d)=>d)
    .on('click', (d)=>{
      if(d=='All'){
        node
          .attr('opacity',1)
      }else{
        node
          .attr('opacity',0.1)
          .filter((f)=>{
            return f.values.map((v)=>v.relationship).indexOf(d)>-1?true:false
          })
          .attr('opacity',1)
      }
    })

  function ticked() {
    node
        .attr("cx", (d)=>d.x)
        .attr("cy", (d)=>d.y);
  }


});
